$var-orange: #FA3F18;
$var-grey: #969696;
  
  .slider {
    width: 300px;
    text-align: center;
    overflow: hidden;
  }

  #gallery-dots {
      margin-top: 30px;
  }
  
  #slides {
    display: flex;
    max-width: 1100px;
    margin: auto;
    overflow-x: auto;
    scroll-snap-type: x mandatory;
    scroll-behavior: smooth;
    img {
        width: 100%;
        border-radius: 50px;
    }
    
  }
  #slides div {
      user-select: none;
    scroll-snap-align: start;
    flex-shrink: 0;
    width: 500px;
    max-width: 100%;
    margin-right: 50px;
    border-radius: 50px;
    background: #eee;
    transform-origin: center center;
    transform: scale(1);
    transition: transform 0.5s;
    position: relative;
    
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 100px;
  }

  .dot {
    cursor: pointer;
    height: 15px;
    width: 15px;
    margin: 0 2px;
    background-color: white;
    border-radius: 50%;
    display: inline-block;
    transition: background-color 0.6s ease;
  }
  
  .active, .dot:hover {
    background-color: $var-orange;
  }
  
